.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
    align-items: flex-start;
    padding-top: 70px;
}
.movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    color: #b2b3bc;
    box-shadow: var(--deepShadow);
    border-radius: 5px;
}
.movie img{
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: var(--deepShadow);
}
.movie .movie__title,
.movie .movie__year{
    margin: 0;
    font-weight: 300;
}
.movie .movie__title{
    margin-bottom: 5px;
    font-size: 25px;
    color: #2c2c2c;
    font-weight: 500;
}
.movie .movie__generes{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}
.movie .movie__generes li{
    margin-right: 10px;
    font-size: 14px;
}
.movie img:hover{
    animation: posterAnimation 1.5s ease-in-out infinite;
}
.movie:hover{
    cursor: pointer;
    border: 1px solid white;
    background-color: rgba(0, 0, 0, 0.6);
    color: rgb(238, 235, 228);
}
.movie:hover .movie__title{
    color: white;
}
@keyframes posterAnimation {
    0%{
        transform: none;
    }
    50%{
        transform: translateY(-10px);
    }
    100%{
        transform: none;
    }
}